// Credit: Nicolas Gallagher and SUIT CSS.

.ratio {
  position: relative;
  width: 100%;

  &::before {
    display: block;
    padding-top: var(--#{Rpprefix}aspect-ratio);
    content: "";
  }

  > * {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
}

@each Rpkey, Rpratio in Rpaspect-ratios {
  .ratio-#{Rpkey} {
    --#{Rpprefix}aspect-ratio: #{Rpratio};
  }
}
