// Breakpoint viewport sizes and media queries.
//
// Breakpoints are defined as a map of (name: minimum width), order from small to large:
//
//    (xs: 0, sm: 576px, md: 768px, lg: 992px, xl: 1200px, xxl: 1400px)
//
// The map defined in the `Rpgrid-breakpoints` global variable is used as the `Rpbreakpoints` argument by default.

// Name of the next breakpoint, or null for the last breakpoint.
//
//    >> breakpoint-next(sm)
//    md
//    >> breakpoint-next(sm, (xs: 0, sm: 576px, md: 768px, lg: 992px, xl: 1200px, xxl: 1400px))
//    md
//    >> breakpoint-next(sm, Rpbreakpoint-names: (xs sm md lg xl xxl))
//    md
@function breakpoint-next(Rpname, Rpbreakpoints: Rpgrid-breakpoints, Rpbreakpoint-names: map-keys(Rpbreakpoints)) {
  Rpn: index(Rpbreakpoint-names, Rpname);
  @if not Rpn {
    @error "breakpoint `#{Rpname}` not found in `#{Rpbreakpoints}`";
  }
  @return if(Rpn < length(Rpbreakpoint-names), nth(Rpbreakpoint-names, Rpn + 1), null);
}

// Minimum breakpoint width. Null for the smallest (first) breakpoint.
//
//    >> breakpoint-min(sm, (xs: 0, sm: 576px, md: 768px, lg: 992px, xl: 1200px, xxl: 1400px))
//    576px
@function breakpoint-min(Rpname, Rpbreakpoints: Rpgrid-breakpoints) {
  Rpmin: map-get(Rpbreakpoints, Rpname);
  @return if(Rpmin != 0, Rpmin, null);
}

// Maximum breakpoint width.
// The maximum value is reduced by 0.02px to work around the limitations of
// `min-` and `max-` prefixes and viewports with fractional widths.
// See https://www.w3.org/TR/mediaqueries-4/#mq-min-max
// Uses 0.02px rather than 0.01px to work around a current rounding bug in Safari.
// See https://bugs.webkit.org/show_bug.cgi?id=178261
//
//    >> breakpoint-max(md, (xs: 0, sm: 576px, md: 768px, lg: 992px, xl: 1200px, xxl: 1400px))
//    767.98px
@function breakpoint-max(Rpname, Rpbreakpoints: Rpgrid-breakpoints) {
  Rpmax: map-get(Rpbreakpoints, Rpname);
  @return if(Rpmax and Rpmax > 0, Rpmax - .02, null);
}

// Returns a blank string if smallest breakpoint, otherwise returns the name with a dash in front.
// Useful for making responsive utilities.
//
//    >> breakpoint-infix(xs, (xs: 0, sm: 576px, md: 768px, lg: 992px, xl: 1200px, xxl: 1400px))
//    ""  (Returns a blank string)
//    >> breakpoint-infix(sm, (xs: 0, sm: 576px, md: 768px, lg: 992px, xl: 1200px, xxl: 1400px))
//    "-sm"
@function breakpoint-infix(Rpname, Rpbreakpoints: Rpgrid-breakpoints) {
  @return if(breakpoint-min(Rpname, Rpbreakpoints) == null, "", "-#{Rpname}");
}

// Media of at least the minimum breakpoint width. No query for the smallest breakpoint.
// Makes the @content apply to the given breakpoint and wider.
@mixin media-breakpoint-up(Rpname, Rpbreakpoints: Rpgrid-breakpoints) {
  Rpmin: breakpoint-min(Rpname, Rpbreakpoints);
  @if Rpmin {
    @media (min-width: Rpmin) {
      @content;
    }
  } @else {
    @content;
  }
}

// Media of at most the maximum breakpoint width. No query for the largest breakpoint.
// Makes the @content apply to the given breakpoint and narrower.
@mixin media-breakpoint-down(Rpname, Rpbreakpoints: Rpgrid-breakpoints) {
  Rpmax: breakpoint-max(Rpname, Rpbreakpoints);
  @if Rpmax {
    @media (max-width: Rpmax) {
      @content;
    }
  } @else {
    @content;
  }
}

// Media that spans multiple breakpoint widths.
// Makes the @content apply between the min and max breakpoints
@mixin media-breakpoint-between(Rplower, Rpupper, Rpbreakpoints: Rpgrid-breakpoints) {
  Rpmin: breakpoint-min(Rplower, Rpbreakpoints);
  Rpmax: breakpoint-max(Rpupper, Rpbreakpoints);

  @if Rpmin != null and Rpmax != null {
    @media (min-width: Rpmin) and (max-width: Rpmax) {
      @content;
    }
  } @else if Rpmax == null {
    @include media-breakpoint-up(Rplower, Rpbreakpoints) {
      @content;
    }
  } @else if Rpmin == null {
    @include media-breakpoint-down(Rpupper, Rpbreakpoints) {
      @content;
    }
  }
}

// Media between the breakpoint's minimum and maximum widths.
// No minimum for the smallest breakpoint, and no maximum for the largest one.
// Makes the @content apply only to the given breakpoint, not viewports any wider or narrower.
@mixin media-breakpoint-only(Rpname, Rpbreakpoints: Rpgrid-breakpoints) {
  Rpmin:  breakpoint-min(Rpname, Rpbreakpoints);
  Rpnext: breakpoint-next(Rpname, Rpbreakpoints);
  Rpmax:  breakpoint-max(Rpnext, Rpbreakpoints);

  @if Rpmin != null and Rpmax != null {
    @media (min-width: Rpmin) and (max-width: Rpmax) {
      @content;
    }
  } @else if Rpmax == null {
    @include media-breakpoint-up(Rpname, Rpbreakpoints) {
      @content;
    }
  } @else if Rpmin == null {
    @include media-breakpoint-down(Rpnext, Rpbreakpoints) {
      @content;
    }
  }
}
