// Shorthand

.fixed-top {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  z-index: Rpzindex-fixed;
}

.fixed-bottom {
  position: fixed;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: Rpzindex-fixed;
}

// Responsive sticky top and bottom
@each Rpbreakpoint in map-keys(Rpgrid-breakpoints) {
  @include media-breakpoint-up(Rpbreakpoint) {
    Rpinfix: breakpoint-infix(Rpbreakpoint, Rpgrid-breakpoints);

    .sticky#{Rpinfix}-top {
      position: sticky;
      top: 0;
      z-index: Rpzindex-sticky;
    }

    .sticky#{Rpinfix}-bottom {
      position: sticky;
      bottom: 0;
      z-index: Rpzindex-sticky;
    }
  }
}
