// Loop over each breakpoint
@each Rpbreakpoint in map-keys(Rpgrid-breakpoints) {

  // Generate media query if needed
  @include media-breakpoint-up(Rpbreakpoint) {
    Rpinfix: breakpoint-infix(Rpbreakpoint, Rpgrid-breakpoints);

    // Loop over each utility property
    @each Rpkey, Rputility in Rputilities {
      // The utility can be disabled with `false`, thus check if the utility is a map first
      // Only proceed if responsive media queries are enabled or if it's the base media query
      @if type-of(Rputility) == "map" and (map-get(Rputility, responsive) or Rpinfix == "") {
        @include generate-utility(Rputility, Rpinfix);
      }
    }
  }
}

// RFS rescaling
@media (min-width: Rprfs-mq-value) {
  @each Rpbreakpoint in map-keys(Rpgrid-breakpoints) {
    Rpinfix: breakpoint-infix(Rpbreakpoint, Rpgrid-breakpoints);

    @if (map-get(Rpgrid-breakpoints, Rpbreakpoint) < Rprfs-breakpoint) {
      // Loop over each utility property
      @each Rpkey, Rputility in Rputilities {
        // The utility can be disabled with `false`, thus check if the utility is a map first
        // Only proceed if responsive media queries are enabled or if it's the base media query
        @if type-of(Rputility) == "map" and map-get(Rputility, rfs) and (map-get(Rputility, responsive) or Rpinfix == "") {
          @include generate-utility(Rputility, Rpinfix, true);
        }
      }
    }
  }
}


// Print utilities
@media print {
  @each Rpkey, Rputility in Rputilities {
    // The utility can be disabled with `false`, thus check if the utility is a map first
    // Then check if the utility needs print styles
    @if type-of(Rputility) == "map" and map-get(Rputility, print) == true {
      @include generate-utility(Rputility, "-print");
    }
  }
}
