@each Rpcolor, Rpvalue in Rptheme-colors {
  .link-#{Rpcolor} {
    color: Rpvalue !important; // stylelint-disable-line declaration-no-important

    @if Rplink-shade-percentage != 0 {
      &:hover,
      &:focus {
        color: if(color-contrast(Rpvalue) == Rpcolor-contrast-light, shade-color(Rpvalue, Rplink-shade-percentage), tint-color(Rpvalue, Rplink-shade-percentage)) !important; // stylelint-disable-line declaration-no-important
      }
    }
  }
}
