// Utility generator
// Used to generate utilities & print utilities
@mixin generate-utility(Rputility, Rpinfix, Rpis-rfs-media-query: false) {
  Rpvalues: map-get(Rputility, values);

  // If the values are a list or string, convert it into a map
  @if type-of(Rpvalues) == "string" or type-of(nth(Rpvalues, 1)) != "list" {
    Rpvalues: zip(Rpvalues, Rpvalues);
  }

  @each Rpkey, Rpvalue in Rpvalues {
    Rpproperties: map-get(Rputility, property);

    // Multiple properties are possible, for example with vertical or horizontal margins or paddings
    @if type-of(Rpproperties) == "string" {
      Rpproperties: append((), Rpproperties);
    }

    // Use custom class if present
    Rpproperty-class: if(map-has-key(Rputility, class), map-get(Rputility, class), nth(Rpproperties, 1));
    Rpproperty-class: if(Rpproperty-class == null, "", Rpproperty-class);

    // Use custom CSS variable name if present, otherwise default to `class`
    Rpcss-variable-name: if(map-has-key(Rputility, css-variable-name), map-get(Rputility, css-variable-name), map-get(Rputility, class));

    // State params to generate pseudo-classes
    Rpstate: if(map-has-key(Rputility, state), map-get(Rputility, state), ());

    Rpinfix: if(Rpproperty-class == "" and str-slice(Rpinfix, 1, 1) == "-", str-slice(Rpinfix, 2), Rpinfix);

    // Don't prefix if value key is null (e.g. with shadow class)
    Rpproperty-class-modifier: if(Rpkey, if(Rpproperty-class == "" and Rpinfix == "", "", "-") + Rpkey, "");

    @if map-get(Rputility, rfs) {
      // Inside the media query
      @if Rpis-rfs-media-query {
        Rpval: rfs-value(Rpvalue);

        // Do not render anything if fluid and non fluid values are the same
        Rpvalue: if(Rpval == rfs-fluid-value(Rpvalue), null, Rpval);
      }
      @else {
        Rpvalue: rfs-fluid-value(Rpvalue);
      }
    }

    Rpis-css-var: map-get(Rputility, css-var);
    Rpis-local-vars: map-get(Rputility, local-vars);
    Rpis-rtl: map-get(Rputility, rtl);

    @if Rpvalue != null {
      @if Rpis-rtl == false {
        /* rtl:begin:remove */
      }

      @if Rpis-css-var {
        .#{Rpproperty-class + Rpinfix + Rpproperty-class-modifier} {
          --#{Rpprefix}#{Rpcss-variable-name}: #{Rpvalue};
        }

        @each Rppseudo in Rpstate {
          .#{Rpproperty-class + Rpinfix + Rpproperty-class-modifier}-#{Rppseudo}:#{Rppseudo} {
            --#{Rpprefix}#{Rpcss-variable-name}: #{Rpvalue};
          }
        }
      } @else {
        .#{Rpproperty-class + Rpinfix + Rpproperty-class-modifier} {
          @each Rpproperty in Rpproperties {
            @if Rpis-local-vars {
              @each Rplocal-var, Rpvariable in Rpis-local-vars {
                --#{Rpprefix}#{Rplocal-var}: #{Rpvariable};
              }
            }
            #{Rpproperty}: Rpvalue if(Rpenable-important-utilities, !important, null);
          }
        }

        @each Rppseudo in Rpstate {
          .#{Rpproperty-class + Rpinfix + Rpproperty-class-modifier}-#{Rppseudo}:#{Rppseudo} {
            @each Rpproperty in Rpproperties {
              @if Rpis-local-vars {
                @each Rplocal-var, Rpvariable in Rpis-local-vars {
                  --#{Rpprefix}#{Rplocal-var}: #{Rpvariable};
                }
              }
              #{Rpproperty}: Rpvalue if(Rpenable-important-utilities, !important, null);
            }
          }
        }
      }

      @if Rpis-rtl == false {
        /* rtl:end:remove */
      }
    }
  }
}
