// stylelint-disable function-name-case

// All-caps `RGBA()` function used because of this Sass bug: https://github.com/sass/node-sass/issues/2251
@each Rpcolor, Rpvalue in Rptheme-colors {
  Rpcolor-rgb: to-rgb(Rpvalue);
  .text-bg-#{Rpcolor} {
    color: color-contrast(Rpvalue) if(Rpenable-important-utilities, !important, null);
    background-color: RGBA(Rpcolor-rgb, var(--#{Rpprefix}bg-opacity, 1)) if(Rpenable-important-utilities, !important, null);
  }
}
